.App {
  text-align: center;
  position: absolute;
  height: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container {
  display: flex;
  width: 390px;
  height: 600px;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 15px;
  margin: 10px;
  background-color: #eee;
  padding: 20px;
  box-sizing: border-box;
}
.container.start {
  height: 300px;
}
.container > div {
  width: 100%;
}

.container-question {
  width: 100%;
  height: 390px;
}

.question-container {
  border: 1px solid white;
  padding: 20px;
  margin-top: 20px;
}
.question-container:last-child {
  margin-bottom: 20px;
}

.picture {
  background-image: url(assets/images/picture.jpg);
  display: flex;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 390px 290px;
  height: 290px;
  width: 390px;
  border-radius: 15px;
  margin: 10px;
}
.picture + button {
  margin-top: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
